/* ------------------------------------------------------------ *\
	Game
\* ------------------------------------------------------------ */

.game {
	width: 1300px;
	margin: 0 auto;
	padding: 50px 0;
	overflow: hidden;
	text-align: center;
	background: url(../images/background.jpg) no-repeat 0 0;
	background-size: contain;
}

.is-running .player:nth-child(2),
.is-running .player {
	transform: none;
}

.is-running .player .player__score {
	opacity: 1;
	transform: none;
}
