.player {
  width: 500px;
  height: 540px;
  background: url(../images/player1.png) no-repeat 0 0;
  background-size: 500px 2160px;
  position: relative;
  display: inline-block;
  vertical-align: bottom;
  transform: translateX(-400px);
  transition: transform .6s 1s; }
  .player .player__score {
    position: absolute;
    top: 0;
    right: 100%;
    text-align: center;
    background: #fff;
    border-radius: 4px;
    width: 60px;
    height: 60px;
    line-height: 60px;
    font-weight: bold;
    font-size: 26px;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity .3s 2s, transform .3s 2s; }
  .player .player__alert {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, sans-serif;
    color: red;
    font-size: 38px;
    font-weight: bold;
    position: absolute;
    top: 50px;
    left: 100px;
    opacity: 0; }

.player:nth-child(2) {
  background-image: url(../images/player2.png);
  margin-left: -280px;
  transform: translateX(400px);
  transition-delay: 1.3s; }
  .player:nth-child(2) .player__score {
    left: 100%;
    right: auto; }
  .player:nth-child(2) .player__alert {
    left: auto;
    right: 100px; }

.player.is-attacking {
  background-position: 0 -1080px; }

.player.is-defending {
  background-position: 0 -540px; }

.player.is-defeated {
  background-position: 0 -1620px; }

.player.got-hit .player__alert {
  animation: blink .3s; }

